setSelectedGuid()
{


var newGuid = this.viewer.props.pubsub[this.viewer.props.guidselector];
            if ((newGuid!=null) && (newGuid!=this.selectedGuid) )
            {
              var child=this.viewer.ifcdb.getChild(newGuid);
              if (child!=null)
              {
              
                var mesh =new Mesh();
                mesh.geometry=child.geometry;
                mesh.name=child.name;
                var par=this.viewer.props["guidpublishparameter"];
                if (par!=null)
              {
                this.viewer.props.publish(par,child.name);
                this.showSelectChild(mesh);
              }
              else
              {
                   console.log("could not find child");
              }


            }
          }



}
mouseDoubleClick(event)
{
  if (this.selectedChild!=null)
  {
    this.zoomAt(this.selectedChild,4);
  }
  
}

mouseclick(event)
{

 if (this.orbiting)return;
 event.preventDefault();
var intersects = this.getIntersection(event);
if (intersects==null) 
{
  if (this.selectedChild!=null)
  {
    this.scene.remove(this.selectedChild);
    this.selectedGuid=null;
  }
return ;
}


if (intersects.length>0)
{
 
  var child = intersects[0].object;

  this.showSelectChild(child);

  var par=this.viewer.props["guidpublishparameter"];
  if (par!=null)
  {
      this.viewer.props.publish(par,intersects[0].object.ifcguid);
  }
//guidpublishparameter




}
else
{
  if (this.selectedChild!=null)
  {
   // this.scene.remove(this.selectedChild);
  }
}



}


showSelectChild(child)
{

// console.log("geen par gevonden ",intersects[0].object)
child=child.clone();

//console.log(child,this.selectedChild);

  if ((this.selectedChild!=null) && (child==this.selectedChild.children[0])) { 
  //  console.log("show selected child is already selected")
    return;
  }
  
      if (child.orgMatrix==null)
      { child.orgMatrix=child.matrix;

        //child.matrix.multiply(root.matrix );
      }
  
 // console.log("Found a child",child,intersects);
if (child.orgMaterial==null) child.orgMaterial=child.material;
child.material=new MeshPhongMaterial({color:0xff0000,specular: 0xaaaaaa,shininess: 1000,emissive:0xff0000});
child.material.opacity=0;
child.renderOrder=11;
if (this.selectedChild!=null)
{
  this.scene.remove(this.selectedChild);
  this.selectedGuid=null;
}
var root =this.viewer.ifcdb.getNewRootGroup();
this.selectedChild=root;
root.add(child)
this.selectedGuid=child.name;
this.scene.add(root);

this.setAnimeerAantal(1);


}



zoomAt = (target,distance)=>{
  console.log("zoom at");
  if (distance==null) distance=1;
	var camera=this.camera;
  //use this code for a object size agnostic solution; build a bounding box around your object you want to zoom and  get its size: 
const aabb = new Box3().setFromObject( target );
const center = aabb.getCenter( new Vector3() );
var size = aabb.getSize( new Vector3() );


  //now use gsap and not tweenjs.. never use tweenjs again. never. 
gsap.to( camera.position, {
duration: 1,
x: center.x,
y: center.y+3, // place camera a bit higher than the object
z: center.z + size.z+distance, // maybe adding even more offset depending on your model
onUpdate: function() {
camera.lookAt( target.position ); //important 
}
} );
var orbitControls= this.controls;
this.setAnimeerAantal(1000);
gsap.to(  this.controls.target, {
duration: 1,
x: center.x,
y: center.y, //set the center of the controler to the zoomed object 
z: center.z , // no distance needed
onComplete: ()=>{
orbitControls.enabled = true; // activate the controler again after animation
this.setAnimeerAantal(10);
}
} );
}






