import { IFCLoader  } from "web-ifc-three/IFCLoader";

class IFCFile
{

    ifcdb=null;
    name=null;
    visible=true;
    root=null;
    allBFGs={};
    allFirstChild={}
    allFirstMaterial={};

   




    static wasmLoaded=false;


  constructor(ifcdb,file,rv)
{
  this.ifcdb=ifcdb;
  var loader = new IFCLoader();
  this.name=file;
  var me=this;
  this.loading=true;
  var me=this;
   
      loader.ifcManager.setWasmPath("../../../bb/wasm/").then(function(){
        IFCFile.wasmLoaded=true;
        me.loadReallyIFC(loader,file,rv)
  });

}



loadReallyIFC(loader,file,rv)
{
    loader.setWithCredentials(true);
    var me=this;
   
    loader.load(file,function(gltf)
    {
       console.log(gltf);
       var materials =gltf.mesh.material;
       var groups=gltf.mesh.groups; //{ start: 0, count: 3450, materialIndex: 0 }


       me.allBFGs["default"]=gltf;
       me.allFirstChild["default"]=gltf.material.color;
       me.allFirstMaterial["default"]=gltf.material;
      
      me.loading=false;
      if (rv!=null){rv.call(me);}
    });
}

}

export default IFCFile