
import { ConstructionOutlined } from "@mui/icons-material";
import { tr } from "date-fns/locale";
import {Color,MeshPhysicalMaterial,Mesh,BufferGeometry,MeshToonMaterial, MeshPhongMaterial,MeshLambertMaterial } from "three";


class IFCDBSparql
{
    ifcdb=null;
    guid_label={};
    colorMat={};
    latestData=null;
    guid_color=null;

constructor(ifcdb)
{
  this.ifcdb=ifcdb;
}

useSparqlColors()
{
  try
  {
    if (this.ifcdb.viewer.props.data!=null)
    {
      var results=this.ifcdb.viewer.props.data.results;
      if (results.bindings.length>0) return true;
      this.latestData=null;

    }
  }
  catch(e)
  {}
  return false;
}

amoutOfSparqlResults()
{
  try
  {
  if (this.ifcdb.viewer.props.data!=null)
  {
    var results=this.ifcdb.viewer.props.data.results;
    return results.bindings.length;
  }
}
catch(e)
{}
  return 0;
}

getLabel(guid)
{
  return this.guid_label[guid];
}



processSparqlResults()
{
  var guid_color = {};
  this.guid_color=guid_color;
  this.guid_label={};
  try
  {
    var results=this.ifcdb.viewer.props.data.results;
    for (var n in results.bindings)
    {
         var binding = results.bindings[n];
         var guid=binding.guid.value;
         var color=binding.color.value;
         guid_color[guid]=color;
         try{var label = binding.label.value; this.guid_label[guid]=label;}catch(e){}
    }
  }
  catch(e)
  {}
  return guid_color;
}

getColor(value)
{
  try
  {
      if ( (value.startsWith("#")) && (value.length==7) )
    {
       return value;
    }
  }
  catch(e){}
   try
   {
    return this.stringToColour(value);
   }
   catch(e){}

  return "#ff0000";
}

stringToColour(str) {
  var hash = 0
  for (let i = 0; i < str.length; i++) {
    hash = str.charCodeAt(i) + ((hash << 5) - hash)
  }
  var colour = '#'
  for (let i = 0; i < 3; i++) {
    var value = (hash >> (i * 8)) & 0xff
    colour += ('00' + value.toString(16)).substr(-2)
  }
  return colour
}
 componentToHex(c) {
  var hex = c.toString(16);
  return hex.length == 1 ? "0" + hex : hex;
}

 rgbToHex(r, g, b) {
  return "#" + this.componentToHex(r) + this.componentToHex(g) + this.componentToHex(b);
}





needsUpdate()
{
  //console.log(this.latestData,this.ifcdb.viewer.props.data);
  try {if (this.latestData==this.ifcdb.viewer.props.data) return false;}
  catch(e ){}
  return true;
}

setSparqlColors_double(guid_color)
{
 console.log("double coloring strategy");
  try
  {
       for (var fn in this.ifcdb.ifcfiles)
        {
          var ifc = this.ifcdb.ifcfiles[fn];
          if (!ifc.visible) continue;
          ifc.visualize(true);
          for (var n in ifc.allNodes)
          {
    
                let child = ifc.allNodes[n];
                if (child.geometry!=null)
                {
                  var ifcguid=child.name;
                  var colorString=guid_color[ifcguid];
                  if (colorString!=null) 
                  {
                      let color=this.getColor(colorString);
                      var mat = new  MeshPhysicalMaterial({color:color});
                      mat.vertexColors=false;
                      var g=child.geometry.clone();
                      var cchild2=new Mesh();
                      cchild2.geometry=g;
                      cchild2.material=mat;
                      this.ifcdb.rootGroup.add(cchild2);
                   }
                }
        
          }     
        }

       this.ifcdb.viewer.viewer.zoomAt(this.ifcdb.rootGroup);


       


  }
  catch(e)
  {
     console.log(e);
     this.ifcdb.defaultColor();
  }
}




setSparqlColors_ReOrganise(guid_color)
{
  console.log("reorganise coloring strategy");
  try
  {
  

    var colorMat={};
    this.colorMat=colorMat;
     this.hash={};
    var hash=this.hash;

        hash["default"]=[];
        colorMat["default"]=this.ifcdb.getDefaultColor("#aaaaaa");
   
        for (var fn in this.ifcdb.ifcfiles)
        {
          var ifc = this.ifcdb.ifcfiles[fn];
          if (!ifc.visible) continue;
    
          for (var n in ifc.allNodes)
          {
    
                let child = ifc.allNodes[n];
                if (child.geometry!=null)
                {
                  var ifcguid=child.name;
                  var colorString=guid_color[ifcguid];
                  if (colorString==null) colorString="default";
                  var ar=hash[colorString];
                   if (ar==null)
                   {
                    ar=[];
                    hash[colorString]=ar;
                    let color=this.getColor(colorString);
                    var mat = new  MeshPhysicalMaterial({color:color});
                    mat.vertexColors=false;
                    colorMat[colorString]=mat;
                   }
                 // ar.push(child.geometry.clone()); //not sure about this
                 ar.push(child.geometry);
                }
        
          }     
        }
    // console.log(hash,colorMat);
        this.ifcdb.processColoring(hash,colorMat);

  }
  catch(e)
  {
     console.log(e);
     this.ifcdb.defaultColor();
  }
}



setSparqlColors()
{
  if (this.ifcdb.loadingInProgress) {
    //console.log("no sparql colors because of loading =true");
    return;
  }
 // console.log("set sparql colors");
  try {this.latestData=this.ifcdb.viewer.props.data}catch(e){}
  
    var guid_color =this.processSparqlResults();
    var amount=this.amoutOfSparqlResults();
   if (amount<100)
   {
    
        this.setSparqlColors_double(guid_color);
   }
   else
   {
        this.setSparqlColors_ReOrganise(guid_color); //good
   }
  
}


}
export default IFCDBSparql;

