import { ConstructionOutlined } from "@mui/icons-material";
import {Box3,Cache,Object3D,Vector3, REVISION,Matrix4,BufferGeometry,LoadingManager } from "three";
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader'
import { KTX2Loader } from 'three/examples/jsm/loaders/KTX2Loader.js';
import { DRACOLoader } from 'three/examples/jsm/loaders/DRACOLoader.js';

import { MeshoptDecoder } from 'three/examples/jsm/libs/meshopt_decoder.module.js';

class IFCGLBFile
{
    ifcdb=null;
    name=null;
    gltf =null;
    visible=true;
    allBFGs={};
    allNodes=[];
    allFirstChild={}
    allFirstMaterial={};
    loading=null;
    defaultChilds=null;

    simpleMode=false;



  

  //  size={};
   // center={};

   
constructor(ifcdb,file,rv,simpleMode)
{
  const MANAGER = new LoadingManager();
  const THREE_PATH = `https://unpkg.com/three@0.${REVISION}.x`
  const DRACO_LOADER = new DRACOLoader( MANAGER ).setDecoderPath( `${THREE_PATH}/examples/jsm/libs/draco/gltf/` );
  const KTX2_LOADER = new KTX2Loader( MANAGER ).setTranscoderPath( `${THREE_PATH}/examples/jsm/libs/basis/` );
  Cache.enabled = true;
  this.ifcdb=ifcdb;
  //Object3D.DefaultUp.set(0, 0, 1);
  if (simpleMode==true) {this.simpleMode=true;}
  var loader = new GLTFLoader();
  loader.setCrossOrigin('anonymous');
  loader.setMeshoptDecoder( MeshoptDecoder );
  loader.setDRACOLoader( DRACO_LOADER )
  loader.setKTX2Loader( KTX2_LOADER.detectSupport( this.ifcdb.viewer.viewer.renderer ) )


  loader.setWithCredentials(true);
  loader.manager.file=file;
  loader.setResourcePath(file);


  this.name=file;
  var me=this;
  this.loading=true;
try
{
  console.log("loading "+file);
  loader.setCrossOrigin("*");
  loader.loadAsync(file).then(function(gltf)
  {
     console.log("loaded "+file,gltf);
      me.gltf=gltf;
      if (!me.simpleMode)
      {
        if (!me.needsTranslation)
        {
           me.processGLTF(gltf);
        }
        else
        {
          console.log("glb file needs translation so not processed");
          me.processGLTF(gltf,me.getCenter());
        }
      }
      me.loading=false;
      if (rv!=null){rv.call(me);}
   },function (progress){
    //console.log(progress);
  },function(error)
    {
      console.log("error",error);
      window.alert("error loading file");
      me.loading=false;
      if (rv!=null){rv.call(me);}

    }
  
   ).catch(err => {console.log(err); alert("could not load 3d file")});
  }
  catch(e)
  {
    window.alert("error loading model ");
    me.loading=false;
    if (rv!=null){rv.call(me);}
  }
}

getCenter()
{
  console.log(this,this.gltf);
  const aabb = new Box3().setFromObject(this.gltf.scene);
  const center = aabb.getCenter( new Vector3() );
  return center;
}

needsTranslation()
{
 
  const center = this.getCenter();
 
  if ((Math.abs(center.x)>1000) || (Math.abs(center.y)>1000) || (Math.abs(center.z)>1000) )
  {
  
    return true;
  }
  else
  {
    console.log("no need for extra translations");
  }
  return false;
}



addDefaultToScene()
{
  console.log("add ",this.name," to the scene" );
  this.ifcdb.rootGroup.add(this.gltf.scene);
 // console.log(this.gltf.scene);
}
getDefaultScene()
{
  return this.gltf.scene;
}



getChild(guid)
 {
      for (var n in this.allNodes)
      {
          var child = this.allNodes[n];
          if (child.name==guid)
          return child;
      }
    return null;
  }

processGLTF(gltf,ncenter)
{
 
   this.allBFGs={};
   this.allNodes=[];
   this.allFirstChild={}
   this.allFirstMaterial={};
   this.loading=null;
   this.defaultChilds=null;

   this.resetMatrix(gltf.scene);
if (ncenter!=null)
{
 
  console.log("translating the scene ",this.name);
  this.ncenter=ncenter;
 

}
 
/*
 const aabb = new Box3().setFromObject( gltf.scene );
  const center = aabb.getCenter( new Vector3() );
  var size = aabb.getSize( new Vector3() );
*/
 
      for (var n in gltf.scene.children )
      {
                let child = gltf.scene.children[n];
                this.processGLTFElement(child,ncenter);
      }


      console.log("ready processing gltf");

}

resetMatrix(child)
{
             child.matrix=new Matrix4();
             child.matrixWorld=new Matrix4();
             if (child.parent!=null)
             {
                this.resetMatrix(child.parent);
             }
}

processGLTFElement(child,ncenter)
{
     if ( (child.isMesh) && (child.isObject3D)  && (child.geometry!=null) && (child.children.length==0))
     {
         if (child.geometry.isBufferGeometry)
         {
          
         }
         else
         {
          console.log("dit heeft een andere geometry ",child.geometry);
         // child.removeFromParent();
          return;
         }
     }
  

      let cid="default";
      let color={r:1,b:1,g:1};
      try
      {
           color=child.material.color;
           cid= color.r+"_"+color.g+"_"+color.b;
      }
      catch(e)
      {
     //  console.log(e,child);


      }
      var ar=this.allBFGs[cid];
 
      if (ar==null)
      {
        ar=[];
        this.allBFGs[cid]=ar;
        this.allFirstChild[cid]=color;
        this.allFirstMaterial[cid]=child.material;
       
      }
     

     

      child.updateMatrix(true);
      child.updateMatrixWorld(true);
      
     
        if (child.children!=null)
        {
        
          
          for (var n in child.children)
          {
                  var sub=child.children[n];
                  this.processGLTFElement(sub,ncenter);
              
          }
          if (child.geometry==null)
          {
            return;
          }
         // return;
        }

             var g = child.geometry;
             g=g.clone(); // geometry can be re-used by other childs
             var matrix = child.matrixWorld.clone();
             child.geometry=g;
         
             if (ncenter!=null)
             {
                  var matrixT=new Matrix4();
                  matrixT.makeTranslation(-ncenter.x,-ncenter.y,-ncenter.z);
                  matrix=matrixT.multiply(matrix);
             }
             g=g.applyMatrix4(matrix);
         
     
        ar.push(g);
       g.guid=child.name;

        this.allNodes.push(child); //for picking and for reference
        this.aantal++;
        this.subAantal++;
        if (this.subAantal>1000)
        {
         // console.log(child);
         //  console.log(this.aantal+" objecten verwerkt. nog niet klaar");
         //   console.log(this.tobeprocessedBindings,this.aantalBindings);
            this.subAantal=0;
        }
     

    }


    visualize(b)
    {
      this.visible=b;
       if (this.defaultChilds==null)
       {

        let colorMat={};
        for (var key in this.allBFGs)
          {
            let color=this.allFirstChild[key];
            let mat2 =this.allFirstMaterial[key];
            var mat=this.ifcdb.getDefaultColor(color,mat2);
            colorMat[key]=mat;
          }
          this.defaultChilds=this.ifcdb.processColoring(this.allBFGs,colorMat);
       }

     //   console.log(this.defaultChilds);
     console.log(this.name+" visualise "+b);
      for (var n in this.defaultChilds)
      {
        this.ifcdb.rootGroup.add( this.defaultChilds[n]);
      }
    }

    
  }

  export default IFCGLBFile
