import React, {useRef, Component } from 'react'
 //trheejs version
import {Color,Mesh,BufferGeometry,PointLight, Vector2,Raycaster,MeshToonMaterial, MeshPhongMaterial,MeshLambertMaterial,SpotLight,AmbientLight, AxesHelper, DirectionalLight, GridHelper, PerspectiveCamera, Scene, WebGLRenderer,Object3D } from "three";
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader'
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";

import { IFCLoader  } from "web-ifc-three/IFCLoader";

import IFCDB from './IFCDB';

import styles from './BBThreeJSViewer.module.css'
import ThreeJSViewer from './ThreeJSViewer';

import PublishUtils from '../../../../helpers/PublishUtils';

import gearWhite from '../../../../images/gear-white.svg'
import gearBlue from '../../../../images/gear-blue.svg'
import { connect } from "react-redux"




//import { acceleratedRaycast, computeBoundsTree, disposeBoundsTree } from "three-mesh-bvh";




class BBThreeJSViewer extends Component 
{
static idN=0;
    
    ifcdb=new IFCDB();
    viewer=null;
  
   canvasDiv = React.createRef();
    onResize=() => {
      // on resize logic
      console.log("resized");
    }


    /*constructor(props)
    {
      super(props);
    
    }
    */
    
    resize=()=>
    {
      console.log("resize event");


     


    }

    
   
      componentDidMount() 
    {
     
      const currentCanvasDiv = this.canvasDiv.current;
     var me=this;
      var resizeObserver = new ResizeObserver((entries) => {
      try
      {
         me.viewer.resize();
      }
      catch(e){}

      });
      resizeObserver.observe(currentCanvasDiv);


     // const element = this.ref.current;
     // element.addEventListener('resize', (event) => console.log(event.detail));
              this.viewer=new ThreeJSViewer();
               this.viewer.initViewer(this);
                this.ready();
               // this.ifcLoader = new IFCLoader();

              //  var o=this.ifcLoader.ifcManager.setWasmPath("../../bb/wasm/").then(function(){me.ready();});
    }




    ready()
    {
      this.processSparqlResults=false;
      this.updateRender();

      
    }


    getGLBFilesFromProps(props)
    {
      var prefix =props["glb files prefix"];
      if (prefix==null)prefix="";
      var files = props["glb files"];
      var files2=[];
      if (files!=null)
      {
        for (var n in files)
        {
          var file=files[n];
        
          var file2=PublishUtils.processStringForParameters({props:props},file);

          if (file2!=null)
          {
            var files22= file2.split(",");
            for (var nn in files22)
            {
              var file3 = files22[nn];
              file3=file3.replace("\"","").replace("\"","").replace("<","").replace(">","").replace("\\\"","").replace("\\\"","");
              if (file3.length>4)
              {
                files2.push(prefix+file3);
              }
            }
          }
          
        }
      }
      return files2;
    }


    shouldComponentUpdate(props,state)
    {
        //no unless
       
        try
        {
          var a1=this.getGLBFilesFromProps(this.props);
          var a2=this.getGLBFilesFromProps(props);
          if (JSON.stringify(a1)!=JSON.stringify(a2)) return true;
        }
        catch(e) {}

        try
        {
           if (this.props.data!=props.data) 
           {
          //  console.log("data change so update is necessary ");
            return true;
           }
        }
        catch(e) {}

        try{
        var sel = this.props.guidselector;
        var sel2 = this.props.pubsub[sel];

        var sela = props.guidselector;
        var sela2 = props.pubsub[sela];
        if (sela2!=sel2) return true;
      }
      catch(e) {}
   

    
       return false;

    }

   


    loadTestIFC() {

      var me=this;
var files=[];
files.push("SITE_HOW.ifc");
if (false)
{
files.push("SITE_HOW_CTRAY.ifc");
files.push("SITE_HOW_EQUIPMENT.ifc");
files.push("SITE_HOW_EQUIPMENT2.ifc");
files.push("SITE_HOW_EXTRAS_HOA.ifc");
files.push("SITE_HOW_HVAC.ifc");
files.push("SITE_HOW_PENETRATIONS.ifc");
files.push("SITE_HOW_PIPING.ifc");
}

if (false)
{

files.push("SITE_HOW_STRUCTURE.ifc");
files.push("SITE_HOW_STRUCTURE1.ifc");
files.push("SITE_HOW_STRUCTURE2.ifc");
files.push("SITE_HOW_STRUCTURE3.ifc");
files.push("SITE_HOW_STRUCTURE4.ifc");
}





for (var n in files)
{
    var file= files[n];
     me.loadIFC("http://localhost:3000/temp/"+file);
}
   

//});
}

loadIFC(file)
{
    var me=this;
    
  //  console.log(file);

  
    this.ifcLoader.load(file, (ifcModel) => me.scene.add(ifcModel));
}



updateRender(force)
{
 
  if (this.viewer!=null)
  {
      if ( (this.viewer.renderer!=null) && (this.processSparqlResults) )
      {
       
        this.ifcdb.processSparqlResults(this);
      }
      else
      {
        if (this.viewer.renderer!=null) 
        {
          var files2=this.getGLBFilesFromProps(this.props);
          if (this.isEmpty(files2))
            {
             
              if (!this.ifcdb.isEmpty())
              {
               
                this.viewer.controls.enabled=false;
                this.ifcdb.setVisible(false);
                this.ifcdb.emptyScene();
               
                this.ifcdb.processedAll();

                
              }
             // console.log("returning ");
              return;
            }
          
          if ((force) || (this.ifcdb.shouldLoadNewFiles(files2)))
          {
            this.viewer.controls.enabled=false;
            this.props.setLoading(true);
             
            this.ifcdb.loadGLBFiles(this,files2);
           // console.log("returning 2");
            return;
          }

          if (this.ifcdb.ifcdbsparql.needsUpdate())
          {
            this.viewer.controls.enabled=false;
            this.props.setLoading(true);
          //  console.log("triggered setSparql update");
            this.ifcdb.processedAll(true); // reuse pickgroup if possible
             return;
          }
          if (this.props.guidselector!=null)
          {
            this.viewer.setSelectedGuid();
            
          }
          

         
        }
        //console.log("no sparql results ");
      }
}
}

isEmpty(files)
{
  if (files==null) return true;
  if (files.length==0) return true;
  if (files.length==1)
  {
    var file =files[0];
    if (file.includes("http://www.buildingbits.nl/reset"))
    {
      return true;
    }
    if (file.length<3)
    {
      return true;
    }
  }
  return false;

 
               

}


    render() {

        
    
      if (this.ifcdb.loadingInProgress)
      {
            console.log("input changed. we might have to check because loading is true");
            this.ifcdb.inputChanged=true;
      }
      else
      {
        this.updateRender();
      }
      if (this.id==null)
      {
          BBThreeJSViewer.idN++;
        this.id="id"+BBThreeJSViewer.idN;
      }

    
      const tippyID="tippydiv"+this.id;
      const tcid="three-canvas"+this.id;
      const legendID="legenddiv"+this.id;
      
        return (
          
          <div ref={this.canvasDiv} id={tippyID} className={styles.fullWidget} >
           <div   className={ styles.right}>
                <img 
                    src={gearWhite}
                    alt="gear"
                  
                    onClick={() => this.setState({ dialogVisible: true })}
                  />
            </div>

           
           
         
            <canvas   className={styles.fullWidget} style={{position: "relative"}} id={tcid}>


      
            </canvas>
            </div>
        )
      }


      
}

export default connect()(BBThreeJSViewer)
